<script>
import AppSetsList from "@/modules/sets/components/SetsList";
import AppTrainersList from "@/modules/trainers/components/TrainersList";
import AppArticlesList from "@/modules/articles/components/ArticlesList";

export default {
  name: 'AppMain',
  components: {AppArticlesList, AppTrainersList, AppSetsList},
  data: () => ({}),
  computed: {
    selectedCityGuid() {
      return this.$store.state.selectedCityGuid;
    },
    citiesList() {
      return this.$store.state.citiesList;
    }
  },
  methods: {
    selectedCity() {
      return this.citiesList.length && this.citiesList.find(item => item.guid === this.selectedCityGuid).declension;
    }
  }
}
